import React from 'react'
import { withStyles,makeStyles } from '@material-ui/styles';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
  } from "@material-ui/core";


function createData(id,title,url,pubDate){
    return{id,title,url,pubDate};
}

const rows = [
    createData(1, '    ,  11  ','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=022&aid=0003652394',' 20 '),
    createData(2, ' 537,   5 1145()','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=001&aid=0012852705',' 38 '),
    createData(1, '    ,  11  ','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=022&aid=0003652394',' 20 '),
    createData(2, ' 537,   5 1145()','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=001&aid=0012852705',' 38 '),
    createData(1, '    ,  11  ','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=022&aid=0003652394',' 20 '),
    createData(2, ' 537,   5 1145()','https://m.news.naver.com/read?mode=LSD&mid=sec&sid1=102&oid=001&aid=0012852705',' 38 '),
];

const useStyles = makeStyles({
    table:{
        minWidth:700,
    },
});

export default function MedptTable(){
    const classes = useStyles();
    return (
        <div align="center" style={{ display: "inline-block" }}>
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell>index/ <>id</></StyledTableCell>
                        <StyledTableCell>title/ <>title</></StyledTableCell>
                        <StyledTableCell>url/ <>url</></StyledTableCell>
                        <StyledTableCell>pubDate/ <>pubDate</></StyledTableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row)=>(
                        <StyledTableRow key={row.id}>
                            <StyledTableCell component ="th" scope="row">{row.id}</StyledTableCell>
                            <StyledTableCell align="center">{row.title}</StyledTableCell>
                            <StyledTableCell align="center">{row.url}</StyledTableCell>
                            <StyledTableCell align="center">{row.pubDate}</StyledTableCell>        
                            
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
        </div>
    );
}

const StyledTableCell = withStyles(theme => ({
    head:{
        backgroundColor: '#246DFF', 
        color: 'white',
    },
    body:{
        fontSize: 14,
    },

}))(TableCell);

const StyledTableRow = withStyles(theme=>({
    row: {
        '&:hover':{backgroundColor: 'rgba(33,150,243,0.5'}
    }
}))(TableRow);